import * as THREE from 'three'
import { PointerLockControls } from 'three/examples/jsm/controls/PointerLockControls.js'
import {Vector3} from "three";

const Controls = (app) => {

    const controls = new PointerLockControls(app.camera, document.body)

    let objects = []
    let raycaster
    const raycasterHeight = new Vector3(0, -5.5, 0)

    let moveForward = false
    let moveBackward = false
    let moveLeft = false
    let moveRight = false
    let canJump = false
    
    let prevTime = performance.now()
    
    const velocity = new THREE.Vector3()
    const direction = new THREE.Vector3()

    const blocker = document.getElementById('blocker')
    const instructions = document.getElementById('instructions')

    instructions.addEventListener('click', function () {
        controls.lock()
    });

    controls.addEventListener('lock', function () {
        instructions.style.display = 'none'
        blocker.style.display = 'none'
    });

    controls.addEventListener('unlock', function () {
        blocker.style.display = 'block'
        instructions.style.display = ''
    });

    app.scene.add(controls.getObject())

    const onKeyDown = function (event) {
        switch (event.code) {
            case 'ArrowUp':
            case 'KeyW':
                moveForward = true
                break
            case 'ArrowLeft':
            case 'KeyA':
                moveLeft = true
                break
            case 'ArrowDown':
            case 'KeyS':
                moveBackward = true
                break
            case 'ArrowRight':
            case 'KeyD':
                moveRight = true
                break
        }
    };

    const onKeyUp = function (event) {
        switch (event.code) {
            case 'ArrowUp':
            case 'KeyW':
                moveForward = false
                break
            case 'ArrowLeft':
            case 'KeyA':
                moveLeft = false
                break
            case 'ArrowDown':
            case 'KeyS':
                moveBackward = false
                break
            case 'ArrowRight':
            case 'KeyD':
                moveRight = false
                break
        }
    };

    document.addEventListener('keydown', onKeyDown);
    document.addEventListener('keyup', onKeyUp);

    raycaster = new THREE.Raycaster(new THREE.Vector3(), new THREE.Vector3(0, -1, 0), 0, 10);
    
    controls.getObject().position.x = -3
    controls.getObject().position.y = 1
    controls.getObject().position.z = -1
    
    const AddObject = (object) => {
        // objects.push(object)
    }

    const RemoveObjects = (object) => {
        // objects(object)
    }
    
    let intersections = null;
    
    const Update = (elapsedTime) => {
        const time = performance.now();

        if (controls.isLocked === true) {

            raycaster.ray.origin.copy(controls.getObject().position);
            raycaster.ray.origin.y = 5;
            
            intersections = raycaster.intersectObjects(objects);
            
            const delta = (time - prevTime) / 1000;

            velocity.x -= velocity.x * 10.0 * delta;
            velocity.z -= velocity.z * 10.0 * delta;

            direction.z = Number(moveForward) - Number(moveBackward);
            direction.x = Number(moveRight) - Number(moveLeft);
            direction.normalize();

            if (moveForward || moveBackward) velocity.z -= direction.z * 20.0 * delta;
            if (moveLeft || moveRight) velocity.x -= direction.x * 20.0 * delta;
            
            if ((moveForward || moveBackward || moveLeft || moveRight) && intersections.length > 0) {
                if (intersections[0].distance > 0) controls.getObject().position.y = 5.5-intersections[0].distance
            }

            controls.moveRight(-velocity.x * delta);
            controls.moveForward(-velocity.z * delta);
            controls.getObject().position.y += (velocity.y * delta);

        }

        prevTime = time;

    }

    return { Update, AddObject, RemoveObjects }
}

export default Controls
