import './style.css'

import * as THREE from 'three'
import Stats from 'stats.js'

import Setup from './utils/setup'
import Preloader from './utils/preloader'
import SceneLoader from './utils/scene-loader'
import Particles from './utils/particles'
import Lights from './utils/lights'
import Controls from './utils/controls'
import Gui from './utils/gui'

const app = {}
app.textures = {}
app.screens = {}

app.canvas = document.querySelector('canvas.webgl')
app.screens.frontPage = document.getElementById('front-page')
app.screens.blocker = document.getElementById('blocker')
app.screens.loader = document.getElementById('loader')

app.screens.blocker.style.display = 'none'
app.screens.loader.style.display = 'none'

app.gui = Gui(app)

app.Start = () => {
    app.screens.frontPage.style.display = 'none'
    app.screens.blocker.style.display = 'block'
    
    app.loader = SceneLoader(app)
    app.particles = Particles(app)
    // app.lights = Lights(app)
    app.controls = Controls(app)
    
    app.loader.LoadScene('Scene1_draft3.fbx')

    const clock = new THREE.Clock()

    const stats = new Stats()
    stats.showPanel(0) // 0: fps, 1: ms, 2: mb, 3+: custom
    document.body.appendChild(stats.dom)

    const tick = () => {
        stats.begin()
        const elapsedTime = clock.getElapsedTime()
        app.effectComposer.render()
        app.particles.Update(elapsedTime)
        app.controls.Update(elapsedTime)
        window.requestAnimationFrame(tick)
        stats.end()
    }
    tick()
}

app.scene = new THREE.Scene()
app.setup = Setup(app)
app.preloader = Preloader(app);
