import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js'

const SceneLoader = (app) => {
    
    const gltfLoader = new GLTFLoader()
    const fbxLoader = new FBXLoader()
    
    const LoadScene = (filename, scale) => {
        app.screens.loader.style.display = 'block'
        fbxLoader.load(
        `./data/${filename}`,
        (fbx) =>
        {
            app.controls.RemoveObjects(app.sceneGroup.getObjectByName( "Floor" ))
            console.log('success')
            while(app.sceneGroup.children.length > 0) {
                app.sceneGroup.remove(app.sceneGroup.children[0])
            }

            app.sceneGroup.add(fbx)
            fbx.scale.set(0.01, 0.01, 0.01)
            
            app.controls.AddObject(app.sceneGroup.getObjectByName( "Floor" ))
            app.screens.loader.style.display = 'none'
        },
        (progress) =>
        {
            console.log('progress')
            console.log(progress)
        },
        (error) =>
        {
            console.log('error')
            console.log(error)
        })       
    }

    return { LoadScene }
}

export default SceneLoader
