import * as THREE from 'three'

import motesVertexShader from '../shaders/motes/vertex.glsl'
import motesFragmentShader from '../shaders/motes/fragment.glsl'

const Particles = (app) => {
    
    const count = 2000
    
    const particlesGeometry = new THREE.BufferGeometry()
    
    console.log(app.textures.particleTexture)
    
    const particlesMaterial = new THREE.ShaderMaterial({
        depthTest: true,
        depthWrite: false,
        transparent: true,
        vertexShader: motesVertexShader,
        fragmentShader: motesFragmentShader,
        uniforms:
        {
    		opacity: { type: 'f', value: 0.7 },
            uTime: { type: 'f', value: 0 },
            uTimeMult: { type: 'f', value: 0.1 },
            uSize: { type: 'f', value: 1.5 * app.renderer.getPixelRatio() }
        },
    })
    
    const positions = new Float32Array(count * 3)
    const colours = new Float32Array(count * 3)
    const scales = new Float32Array(count)
    
    for(let i = 0; i < count * 3; i++)
    {
        if ( i % 3 === 0 )positions[i] = (Math.random() - 0.5) * 30
        if ( i % 3 === 1 )positions[i] = (Math.random() - 0.5) * 10
        if ( i % 3 === 2 )positions[i] = (Math.random() - 0.5) * 30
        colours[i] = Math.random()
        scales[i] = Math.random() + 10
    }
    
    particlesGeometry.setAttribute('position', new THREE.BufferAttribute(positions, 3))
    particlesGeometry.setAttribute('color', new THREE.BufferAttribute(colours, 3))
    particlesGeometry.setAttribute('aScale', new THREE.BufferAttribute(scales, 1))
    
    const particles = new THREE.Points(particlesGeometry, particlesMaterial)
    app.scene.add(particles)
    
    const Update = (elapsedTime) => {
        particlesMaterial.uniforms.uTime.value = elapsedTime
    }
    
    return { Update }
}

export default Particles
