import * as THREE from 'three'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { SMAAPass } from 'three/examples/jsm/postprocessing/SMAAPass.js'
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass'

const Setup = (app) => {
    
    app.sceneGroup = new THREE.Group();
    app.scene.add(app.sceneGroup)
    
    // Axes Helper
    const axesHelper = new THREE.AxesHelper(2)
    app.scene.add(axesHelper)

    // Sizes
    const sizes = {
        width: window.innerWidth,
        height: window.innerHeight
    }

    // Camera
    app.camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height)
    app.camera.position.z = 3
    app.camera.near = 0.1
    app.camera.far = 10000
    app.scene.add(app.camera)
    
    // Renderer
    app.renderer = new THREE.WebGLRenderer({
        canvas: app.canvas
    })
    app.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    app.renderer.shadowMap.type = THREE.PCFSoftShadowMap
    app.renderer.shadowMap.enabled = true
    
    let RenderTargetClass = null
    
    if(app.renderer.getPixelRatio() === 1 && app.renderer.capabilities.isWebGL2)
    {
        RenderTargetClass = THREE.WebGLMultisampleRenderTarget
        console.log('Using WebGLMultisampleRenderTarget')
    }
    else
    {
        RenderTargetClass = THREE.WebGLRenderTarget
        console.log('Using WebGLRenderTarget')
    }
    
    const renderTarget = new THREE.WebGLRenderTarget(
        800,
        600,
        {
            minFilter: THREE.LinearFilter,
            magFilter: THREE.LinearFilter,
            format: THREE.RGBAFormat,
            encoding: THREE.sRGBEncoding
        }
    )

    app.effectComposer = new EffectComposer(app.renderer, renderTarget)
    app.effectComposer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    app.effectComposer.setSize(sizes.width, sizes.height)
    
    const renderPass = new RenderPass(app.scene, app.camera)
    app.effectComposer.addPass(renderPass)

    const unrealBloomPass = new UnrealBloomPass()
    unrealBloomPass.strength = 0.3
    unrealBloomPass.radius = 1
    unrealBloomPass.threshold = 0.6
    app.effectComposer.addPass(unrealBloomPass)

    if(app.renderer.getPixelRatio() === 1 && !app.renderer.capabilities.isWebGL2)
    {
        const smaaPass = new SMAAPass()
        app.effectComposer.addPass(smaaPass)
    
        console.log('Using SMAA')
    }
    
    // Fog
    app.scene.fog = new THREE.Fog('#762877', 2.0, 22)
    app.renderer.setClearColor('#762877')

    window.addEventListener('resize', () => {
        // Update sizes
        sizes.width = window.innerWidth
        sizes.height = window.innerHeight

        // Update camera
        app.camera.aspect = sizes.width / sizes.height
        app.camera.updateProjectionMatrix()

        // Update renderer
        app.renderer.setSize(sizes.width, sizes.height)
        app.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
        
        // Update Post
        
        app.effectComposer.setSize(sizes.width, sizes.height)
    })

    app.renderer.setSize(sizes.width, sizes.height)
}

export default Setup
