import * as dat from 'dat.gui'

const api = { scene: 'Scene1_draft3.fbx' };

const Gui = (app) => {
    const gui = new dat.GUI({ width: 300 })
    
    const scenes = [ 'Scene1_draft3.fbx', 'test-instance_noPSR.fbx', 'test-instance_wPSR.fbx', 'test-scene.fbx', 'TestScene_Basic.fbx', 'Scene_3_Test_Bake_Refections_11_02_v2.fbx' ];
    const statesFolder = gui.addFolder( 'Scenes' );

    const fileCtrl = statesFolder.add( api, 'scene' ).options( scenes );

    fileCtrl.onChange( function () {
        app.loader.LoadScene(api.scene)
    } );

    statesFolder.open();
    
    return { gui }
}

export default Gui
